import React, { Component } from 'react'
import axios from 'axios'
import moment from 'moment'

const badgeStyle={
    display:"inlineBlock",
    position:"absolute",
    top:"5px",
    right:"15px",
    backgroundColor:"red",
    color:"#fff",
    borderRadius:"15px",
    fontWeight:"bolder",
    wordSpacing:"1px"
}


 class World extends Component {
    
    state={
        worldnews:""
    }

    componentDidMount(){
        axios.get('http://newsapi.org/v2/top-headlines?country=us&category=business&apiKey=b98b346a139444e79813e97b3425770b')
        .then((res)=>{
            this.setState({worldnews:res.data.articles.slice(0,4) })
          
        }).catch((err)=>{
            console.log('exce new error',err)
       })
    }

    render() {
        console.log('world news state lucky guess is ',this.state.worldnews)
        const worldnews=this.state.worldnews;
        const worldnewsPost=worldnews? worldnews.map((el)=>{
            return(
                <div className='col l3 m4 s6' >

<div className="card"  >
                      <div className="card-image">
                               <a href={el.url} >
                                   <img src={el.urlToImage } height="150" width='100' alt='business-news-img'/>
                               </a>
                              <span className=' badge' style={ badgeStyle } >world</span>
                              
                      </div>

                      <div className="card-content">
                        <a href={el.url}  style={{color:"black"}}>{el.title.slice(0,50)+"..."}</a>
                        <a href={el.url}  style={{color:"black"}}><p style={{color:"orange",fontWeight:"bolder"}}>{  moment(el.publishedAt).fromNow() }</p></a>
                        
                      </div>
                 </div>

            </div>
            )

        }):<p>....loading</p>
        return (
              
            <div style={{marginTop:"50px"}}>
               
                <div className='fluid-container #eceff1 blue-grey lighten-5' style={{height:"",padding:"30px 0 50px"}}>
                   <div className='container'>
                         <div className='row'>

                            { worldnewsPost }
                              
                            </div>
                        </div>
                    </div>
             </div>
        )
    }
}



export default World
