import React, { Component } from 'react'
import axios from 'axios'

//import { Link } from 'react-router-dom'

 class Latest extends Component {
    state={
        latestnews:""
    }

    componentDidMount(){
        axios.get('http://newsapi.org/v2/everything?domains=wsj.com&apiKey=b98b346a139444e79813e97b3425770b')
        //axios.get('http://newsapi.org/v2/everything?q=apple&from=2020-07-28&to=2020-07-28&sortBy=popularity&apiKey=b98b346a139444e79813e97b3425770b')
              .then(res=>{
            this.setState({latestnews:res.data.articles.slice(0,4) })
          
        }).catch((err)=>{
            console.log('exce new error',err)
       })
    }
    render() {
        //console.log(this.props)
        const { latestnews }=this.state;

        const showLatest= latestnews?latestnews.map((el)=>{
            return(
                <div className="card col l5" style={{ marginLeft:"20px" }}>
                            
                      <div className="card-image">
                            <a href={ el.url} > <img src={ el.urlToImage } height="150" width='100' alt='latest-news-img'   /></a>
                              
                      </div>

                      <div className="card-content" style={{padding:"5px 0"}}>
                          <a href={ el.url} style={{color:"black"}}> <p>{ el.title.slice(0,40) }....</p> </a>
                      </div>

            </div>
            )

        }):
        (<p>loading....</p>)

        const secondPart=latestnews? latestnews.map((el)=>{
                         return(  <li style={{float:"none"}}>
                                                         <a href={el.url} className='row'>
                                                                  <div className="card-image col l4" >
                                                                        <img src={  el.urlToImage  } height='50' width='80' alt='latest-news-img'   />
                                                                  </div>
                                                                   <div className=" col l8 right" >
                                                                          <p >{ el.title.slice(0,20)}...</p>
                                                                   </div>
                                                         </a>
                            </li>
                         )
        }):<p>...loading</p>

        return (
            <div className='container' style={{marginTop:"px"}}>
              <div className='row' >
                   <div className='col l8'>
                       <div className='row' style={{marginLeft:"5px",marginBottom:"0"}}>
                           { showLatest }
                        </div>
                   </div>
                   

                   <div className='col l4' style={{float:"left"}}>
                      <nav>
                           <ul >
                               {  secondPart }
                               
                          </ul>
                      </nav>

                   </div>

              </div>
                
            </div>
        )
    }
}

export default Latest